import React from "react";
import ChatList from "../components/chat/ChatList";
import Layout from "../layouts/Layout";
import "./Chatone.css";

function ChatOne() {
  return (
    <Layout>
      <div uk-grid="true">
        <div className="uk-width-1-4@s uk-visible@m" style={{minWidth: 300}}>
          <ChatList />
        </div>
        <div
          className="uk-width-expand chat padding-custom"
          style={{ backgroundColor: "#d6d6d6" }}
        >
          <nav
            className="uk-navbar-container uk-padding-small chat__header"
            uk-navbar="true"
          >
            Laptop Needed
          </nav>
          <div
            className="chat__body"
            style={{ overflow: "auto", Height: "77vh" }}
          >
            <div className="chat__message">hello</div>
            <div className="chat__message chat__reciever">hi</div>
            <div className="chat__message">How are you</div>
            <div className="chat__message chat__reciever">good</div>
            <div className="chat__message">you?</div>
            <div className="chat__message chat__reciever">good</div>
            <div className="chat__message">hello</div>
            
          </div>
          <div className="chat__footer" style={{ bottom: 0 }}>
            <form action="">
              <input type="text" placeholder="Type something" />
            <button className="uk-button">Send</button>
            </form>
          </div>
        </div>
      </div>
    </Layout>
  );
}

export default ChatOne;
